@use "../utilities/mixins" as mx;
@use "../selectors" as vars;

// text-small
#{vars.$text-small-selector} {
  @include mx.base-font-configuration(13px, 18px, 300);
  // Commented code is an initial implementation
  // where font-size was changed for mobile, to make it smaller
  // However, we decided to keep it the same for mobile and desktop
  //@include mx.base-font-configuration(10px, 14px, 300);

  // @include mx.on-tablet {
  // @include mx.base-font-configuration(13px, 18px, 300);
  // }
}

// text-regular

#{vars.$text-body-selector} {
  @include mx.base-font-configuration(16px, 24px, 300);
  // @include mx.base-font-configuration(11px, 14px, 300);

  // @include mx.on-tablet {
  // @include mx.base-font-configuration(16px, 24px, 300);
  // }
}

// text tiny
#{vars.$text-extra-small-selector} {
  @include mx.base-font-configuration(12px, 16px, 300);
  // @include mx.base-font-configuration(10px, 14px, 300);

  // @include mx.on-tablet {
  // @include mx.base-font-configuration(12px, 16px, 300);
  // }
}

// Underline Large
#{vars.$text-underline-large-selector} {
  @include mx.base-font-configuration(18px, 28px, 400);
  text-decoration: underline;
  // @include mx.base-font-configuration(13px, 18px, 400);

  // @include mx.on-tablet {
  // @include mx.base-font-configuration(18px, 28px, 400);
  // }
}

// Underline Medium
#{vars.$text-underline-medium-selector} {
  @include mx.base-font-configuration(16px, 24px, 400);
  text-decoration: underline;
  // @include mx.base-font-configuration(11px, 14px, 400);

  // @include mx.on-tablet {
  // @include mx.base-font-configuration(16px, 24px, 400);
  // }
}

// Underline Small
#{vars.$text-underline-small-selector} {
  @include mx.base-font-configuration(13px, 18px, 400);
  text-decoration: underline;
  // @include mx.base-font-configuration(10px, 14px, 400);

  // @include mx.on-tablet {
  // @include mx.base-font-configuration(13px, 18px, 400);
  // }
}

// Quote
#{vars.$quote-selector} {
  @include mx.base-font-configuration(20px, 32px, 400);
